import '../assets/css/Heartbeat.css'
import React, {useState} from 'react'

const HeartModal = ({setOpenHeartModal}) => {
  return (
      <div className = 'modal'>
          <div className = 'modal-container'>
              <div className = 'modal-body'>
                    <p>Cm n mi ngi v  c n y</p>
                    <p>Khi hon thnh xong sn phm ny ti c rt nhiu cm ngh, mt phn  c ghi  bn trong, phn cn li l s 
                        t ho khi mnh c th hon thnh ht c cun ny. y l project u tin m t hon thnh c ti cng 
                        v trong khong thi gian kh ngn i vi t. Lc u t ch tnh dng HTML, CSS, JS n thun thi v Reactjs th t ch 
                        mi tm hiu nn cng khng nm r lm. Nhng ri cui cng t liu mnh dng React lun. Va lm va hc, ci g khng bit 
                        th tra Google :'') Sn phm cng coi nh l kh tt vi d nh ban u ca t.
                    </p>
                    <p>Mt ln na cm n cc bn  c n y. Cm n mi ngi rt nhiu</p>
              </div>
          </div>
      </div>
  );
}

const Heartbeat = ({setOpenHeart}) => {
  const [showHeartModal, setOpenHeartModal] = useState(false)
  return (
      <div id = 'heartbeat-container'>
          <div id = 'heartbeat' onClick = {() => {setOpenHeartModal(true)}} style={{display: showHeartModal ? 'none' : 'block' }}>
              
          </div>
          {showHeartModal && <HeartModal></HeartModal>}
      </div>
  );
}

export default Heartbeat